import { OutRoute } from './index'

export const home = '/home'

export const routes: Array<OutRoute> = [
  {
    path: '/home',
    pageName: 'menu.home',
    component: () => import('../components/page/Home.vue')
  },
  {
    path: '/mind', // 思维导图
    pageName: 'menu.mind',
    component: () => import('../components/page/CodeMind.vue'),
    defaultTag: 'frontend',
    tags: [
      {
        key: 'frontend', // 前端思维导图
        name: 'tab.mind.frontend'
      },
    ]
  },
  {
    path: '/vueSample', // 例子
    pageName: 'menu.vueSample',
    component: () => import('../components/page/Sample.vue'),
    defaultTag: 'vueSample1',
    tags: [
      {
        key: 'vueSample1',
        name: 'tab.vueSample.vueSample1'
      },
      {
        key: 'vueSample2',
        name: 'tab.vueSample.vueSample2'
      },
    ]
  },
  {
    path: '/jsCodeTest', // 测试代码
    pageName: 'menu.jsCodeTest',
    component: () => import('../components/page/TestCode.vue'),
    defaultTag: 'jsCodeTest1',
    tags: [
      {
        key: 'jsCodeTest1',
        name: 'tab.jsCodeTest.jsCodeTest1'
      },
      {
        key: 'jsCodeTest2',
        name: 'tab.jsCodeTest.jsCodeTest2'
      },
    ]
  },
]
